<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Reviews List</h1>
      <div class="chartWrap">
          <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a href="javascript:void(0)" @click="$router.push('/cart')" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <ul>
        <li v-for="(item,index) in result.result" :key="index">
            <div class="coupon_left">
                <span>ADE <b>{{(item.result/100).toFixed(0)}}</b>.{{(item.result/100).toFixed(2).split(".")[1]}}</span>
                <p>Order AED {{(item.condition/100).toFixed(2)}}+</p>
            </div>
            <div class="coupon_right">
                <span>{{item.beginTime}}-{{item.endTime}}</span>
                <p>{{item.name}}</p>
            </div>
        </li>
        
    </ul>
  </div>
</template>

<script>

import {myCoupons} from '@/api/user'
import {mapGetters} from 'vuex'
export default {
  name: 'coupons',
  data () {
    return {
     value:'4',
        result:{}
    }
  },
  computed: {
      ...mapGetters(['indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        myCoupons({pageNo:1,pageSize:10}).then(res=>{
            this.result=res.data.result;
        });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
    ul{
        border-top: solid 10px #F6F6F6;
        padding-top: 13px;
    }
    li{
        height: 98px;
        width: 100%;
        background: url('../common/images/coupons.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;   
        margin-bottom: 8px;
        display: -webkit-box;
    }
    .coupon_left{
        height: 100%;
        width: 39%;
        position: relative;
        color: #fff;
        font-size: 14px;
        span{
            position: absolute;
            padding-top: 16%;
            right: 10px;
        }
        b{
            font-size: 25px;
        }
        p{
            font-size: 12px;
            position: absolute;
            right: 10px;
            bottom: 15px
        }
    }
    .coupon_right{
        width: 61%;
        height: 100%;
        position: relative;
        span{
            display: block;
            color: #b8b8b8;
            font-size: 11px;
            padding-left: 5px;
            padding-top: 19%;
        }
        p{
            position: absolute;
            padding-left: 3px;
            font-size: 9.5px;
            bottom: 10px;
        }
    }
</style>



